* {
  margin: 0;
  padding: 0;
  
}

.font-face-mied {
  font-family: "MiedingerBold";
 }

 .font-face-mort {
  font-family: "MortendExtraBold";
 }

 .font-face-mortbold {
  font-family: "FutoSansBold";
 }

 .font-face-futomedium {
  font-family: "FutoSansMedium";
 }

