/* App Styling */

.cardContainer {
  background-Color: #D5FF3F;
  width: 100%;
  /* // width: 100%; */
  position: relative; 
  z-Index: -3;
  top: 850px;
  min-Width: 720px;
  min-Height: 100%;
  height: 100%;
  
}

.subHeader {
  font-Family: MortendExtraBold;
  width: 100%;
  background-Color: black;
  color: #D5FF3F;
  text-Align: center;
  align-Items: center;
  font-Size: 50px;
  padding: 20px 0;
  white-Space: nowrap;
}

.starPos {
  position: relative;
  top:10px;
  margin: 0px 10%
}






body{
  font-family: "FutoSansMedium";
  background-color: #D5FF3F;
  overflow-x: hidden;
  
}




/* Card Component CSS */



   .card {display: flex;
    flex-Direction: row;
    align-Items: center;
    justify-Content: space-between;
    width: 95%;
    height: 368px;
    background-Color: black;
    z-Index: 1;
    margin-Bottom:30px;
    
  }

  /* .cardImage {
    
  }; */

 .cardText {
    color: #D5FF3F;
    width: 90%;
    padding: 0px 20px 130px 20px;
    position: relative;
    margin-Top: 90px
  }

  .selectionBox {
    background-Color: #D5FF3F;
    color: black;
    position: relative;
    width: 96%;
    height: 70px;
    font-Family: MortendExtraBold;
    font-Size: 50px;
    padding-Top: 20px;
    padding-Left: 45px;
    display: flex;
    flex-Direction: row;
    white-Space: nowrap;
    justify-Content: space-between;
    top: 60px
  }

  .clickBox {
    background-Color: black;
    width: 20px;
    height: 20px;
    padding: 25px;
    margin: 0px 10px 30px 0px;
    position: relative;
    top: -10px
  }

  .FED {
    font-Size: 40px;
    padding-Bottom: 30px;
    white-Space: nowrap;
  }

  .text {
    position: relative;
    padding-Bottom: 40px;
    
  }

  .arrowIcon {
position:relative;
right: 12.5px;
bottom: 11px
  }


